import { createSlice } from "@reduxjs/toolkit";
import images from "../../images";
import { BenefitsSliceState } from "../types";
const {
    bench,
    building,
    fountain,
    bicycle,
} = images;
const initialState: BenefitsSliceState = {benefits: [{
    image:bench,
    description: '    ',
    alt: 'bench',
    width: '80',
    height: '80'
},
{
    image:building,
    description: ' ',
    alt: 'building',
    width: '80',
    height: '80'
},
{
    image:fountain,
    description: '10   ',
    alt: 'fountain',
    width: '80',
    height: '80'
},
{
    image:bicycle,
    description: '6  ',
    alt: 'bicycle',
    width: '80',
    height: '80'
},
] }
const benefitsSlice = createSlice({
    name:'benefits',
    initialState,
    reducers:{},
})
export default benefitsSlice.reducer;